@import "common/styles/sass/variables.scss";

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  outline: none;
}

*, ::after, ::before {
  box-sizing: border-box;
}

a, a:focus, a:hover {
  text-decoration: none;
}

p {
  color: #999;
  line-height: 1.7em;
  margin-top: 0;
  margin-bottom: 1rem;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  font-family: Oswald, sans-serif;
  color: #ffffff;
  font-weight: 500;
  line-height: 1.2;
}

.app > div:nth-child(odd) {
  background-color: $LightBgColor;
}

.app > div:nth-child(even) {
  background-color: $DarkBgColor;
}

@font-face {
  font-family: 'Oswald';
  src: local('Oswald'), url(./fonts/Oswald-VariableFont_wght.ttf) format('truetype');
}

@font-face {
  font-family: 'Heebo';
  src: local('Heebo'), url(./fonts/Heebo-VariableFont_wght.ttf) format('truetype');
}

body {
  margin: 0;
  font-family: Heebo, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1rem;
  font-weight: 400;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

